import { useLoaderData } from "remix";

export const loader = () => {
  return [
    {
      slug: "first-post",
      title: "First post",
    },
    {
      slug: "second-post",
      title: "Second post",
    },
  ];
};

export type Post = {
  slug: string;
  title: string;
};

export default function Posts() {
  const posts: Post[] = useLoaderData();
  console.log(posts);

  return (
    <div>
      <h1 className="text-lg">Posts</h1>
      {posts.map((post) => (
        <div>{post.title}</div>
      ))}
    </div>
  );
}
